/* lato-regular - latin */
@font-face {
	font-family: "Lato";
	font-style: normal;
	font-weight: 400;
	src: url("../webfonts/lato-v20-latin-regular.eot"); /* IE9 Compat Modes */
	src: local(""),
		url("../webfonts/lato-v20-latin-regular.eot?#iefix")
			format("embedded-opentype"),
		/* IE6-IE8 */ url("../webfonts/lato-v20-latin-regular.woff2")
			format("woff2"),
		/* Super Modern Browsers */
			url("../webfonts/lato-v20-latin-regular.woff") format("woff"),
		/* Modern Browsers */ url("../webfonts/lato-v20-latin-regular.ttf")
			format("truetype"),
		/* Safari, Android, iOS */
			url("../webfonts/lato-v20-latin-regular.svg#Lato") format("svg"); /* Legacy iOS */
}
